import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class HttpService {

  private BaseUrl:string='http://localhost:8080/emp/';

  constructor(private http:HttpClient) { }

  LoginCheck(obj){
    return (this.http.post(`${this.BaseUrl}logincheck`,obj));
 }

 GetAllEmployee(){
  return (this.http.get(`${this.BaseUrl}employees`));
 }

 AddEmployee(obj){
   return (this.http.post(`${this.BaseUrl}addEmployee`,obj,{responseType:'text'}));
 }

 GetAllCountries(){
   return (this.http.get(`${this.BaseUrl}countries`));
 }

 getParticularEmployee(id){
  return (this.http.get(`${this.BaseUrl}employee/${id}`));
 }

 UpdateEmployee(obj){
  return (this.http.put(`${this.BaseUrl}employee/${obj.employeeId}`,obj,{responseType:'text'}));
 }

 deleteEmployee(id){
   return (this.http.delete(`${this.BaseUrl}employee/${id}`,{responseType:'text'}));
 }

 changeStatus(obj){
   return (this.http.put(`${this.BaseUrl}emp/${obj.employeeId}`,obj ,{responseType:'text'}));
 }







}
