import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AddemployeeComponent } from './addemployee/addemployee.component';
import { EmployeedetailsComponent } from './employeedetails/employeedetails.component';
import { HomeComponent } from './home/home.component';
import { LoginComponent } from './login/login.component';
import { MyauthGuard } from './myauth.guard';
import { NotfoundComponent } from './notfound/notfound.component';
import { UpdateEmployeeComponent } from './update-employee/update-employee.component';


const routes: Routes = [
  {
    path:"",redirectTo:"/login",pathMatch:"full"
  },
  {
    path:"login",component:LoginComponent
  },
  {
    path:"home",component:HomeComponent,canActivate:[MyauthGuard]
  },
  {
    path:"addEmployee",component:AddemployeeComponent,canActivate:[MyauthGuard]
  },
   {
    path:"employeeDetails/:id",component:EmployeedetailsComponent,canActivate:[MyauthGuard]
  },
  // {
  //   path:"updateEmployee/:id",component:UpdateEmployeeComponent,canActivate:[MyauthGuard]
  // },
  {
    path:"**",component:NotfoundComponent
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
